section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    background-color: #18056a;
}

 
.container {
    width: 100%;
    min-height: 60vh;
    background: #333;
  }
  .image {
    perspective: 5000px;
    width: 50%;
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%, -50%);
    transform-style: preserve-3d;
    cursor: pointer;
  }
  .image img {
    transform: rotateX(70deg) rotateZ(-60deg) translate3d(-120px, 0px, 70px);
    box-shadow: -80px 60px 15px 5px rgba(0,0,0,0.4);
    transition: all .4s;
    transform-style: preserve-3d;
  
  }
  .image:hover img {
    transform: rotateX(0deg) rotateZ(0deg) translate3d(0px, 0px, 0px);
    box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.0);
  }
  
 
  @media (max-width:1100px){
    .image img{
       width:550px;
    }
    .image{
      left:45%;
    }
    
    
}
@media screen and (max-width: 768px){
  .image {
    left: 40%;
    top:70%;
  }
}
p {
    font-size: 18px;
    line-height: 1.5;
    margin-top: 20px;
    color:white;
}
button{
    background-color: #FF854C;
    text-align: center;
    width: 180px;
    padding: 10px;
    border-radius: 90px;
    cursor: pointer;
    margin-top: 10px;
    border: 2px solid white;
    font-size: 17px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
button:hover{
    background-color: white;
    width:250px;
    color: black;
    transition-duration: 1s;

}